import { createElement, ensureElement } from "../utils/utils";
import { Component } from "./base/Component";
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._price = this.container.querySelector('.basket__price');
        this._button = this.container.querySelector('.basket__button');
        this._button.addEventListener('click', () => {
            events.emit('order:open');
        });
        this.items = [];
        this.setDisabled(this._button, true);
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: '–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞  üõí'
            }));
        }
    }
    set total(total) {
        this.setText(this._price, total);
    }
    set selected(value) {
        if (value > 0) {
            this.setDisabled(this._button, false);
        }
        else {
            this.setDisabled(this._button, true);
        }
    }
    renumerateItems() {
        Array.from(this._list.children).forEach((item, index) => {
            item.querySelector(`.basket__item-index`).textContent = (index + 1).toString();
        });
    }
}
