import { AppModelData } from './components/AppModelData';
import { Basket } from './components/Basket';
import { BasketItem } from './components/BasketItem';
import { Card } from './components/Card';
import { Contacts } from './components/Contacts';
import { Order } from './components/Order';
import { Page } from './components/Page';
import { Success } from './components/Success';
import { Modal } from './components/base/Modal';
import { Api } from './components/base/api';
import { EventEmitter } from './components/base/events';
import './scss/styles.scss';
import { API_URL } from './utils/constants';
import { cloneTemplate, createElement, ensureElement } from './utils/utils';
// –®–∞–±–ª–æ–Ω—ã
const cardTemplate = ensureElement('#card-catalog');
const cardPreviewTemplate = ensureElement('#card-preview');
const modalTemplate = ensureElement('#modal-container');
const basketTemplate = ensureElement('#basket');
const cardBasketTemplate = ensureElement('#card-basket');
const orderTemplate = ensureElement('#order');
const contactTemplate = ensureElement('#contacts');
const successTemplate = ensureElement('#success');
const api = new Api(API_URL);
const events = new EventEmitter();
const appData = new AppModelData({}, events);
const page = new Page(document.body, events);
const modal = new Modal(modalTemplate, events);
const basket = new Basket(cloneTemplate(basketTemplate), events);
const order = new Order(cloneTemplate(orderTemplate), events);
const contacts = new Contacts(cloneTemplate(contactTemplate), events);
const success = new Success('order-success', cloneTemplate(successTemplate), {
    onClick: () => {
        events.emit('modal:close');
        modal.close();
    }
});
// –ü–æ–ª—É—á–∞–µ–º –ª–æ—Ç—ã —Å —Å–µ—Ä–≤–µ—Ä–∞
api
    .get('/product')
    .then((res) => {
    appData.setCatalog(res.items);
})
    .catch((err) => {
    console.error(err);
});
// –ò–∑–º–µ–Ω–∏–ª–∏—Å—å —ç–ª–µ–º–µ–Ω—Ç—ã –∫–∞—Ç–∞–ª–æ–≥–∞
events.on('items:changed', () => {
    page.catalog = appData.catalog.map((item) => {
        const card = new Card(cloneTemplate(cardTemplate), 'card', {
            onClick: () => events.emit('card:select', item)
        });
        return card.render({
            id: item.id,
            title: item.title,
            image: item.image,
            category: item.category,
            price: item.price,
        });
    });
});
// –û—Ç–∫—Ä—ã—Ç—å –ª–æ—Ç
events.on('card:select', (item) => {
    appData.setPreview(item);
});
events.on('preview:changed', (item) => {
    const card = new Card(cloneTemplate(cardPreviewTemplate), 'card', {
        onClick: () => {
            events.emit('card:toBasket', item);
        },
    });
    modal.render({
        content: card.render({
            title: item.title,
            image: item.image,
            category: item.category,
            price: item.price,
            description: item.description,
            disabled: (item.price === null),
        })
    });
});
// –î–æ–±–∞–≤–ª–µ–Ω–∏–µ —Ç–æ–≤–∞—Ä–∞ –≤ –∫–æ—Ä–∑–∏–Ω—É
events.on('card:toBasket', (item) => {
    appData.addToBasket(item);
    basket.items = appData.basket.map((item, index) => {
        const basketItem = new BasketItem(cloneTemplate(cardBasketTemplate), {
            onClick: () => events.emit('basket:delete', item)
        });
        return basketItem.render({
            title: item.title,
            price: item.price | 0,
            number: index + 1
        });
    });
    page.counter = appData.getBasketAmount();
    basket.total = appData.getTotalBasketPrice();
    basket.selected = appData.getBasketAmount();
    modal.close();
});
// –ë–ª–æ–∫–∏—Ä—É–µ–º –ø—Ä–æ–∫—Ä—É—Ç–∫—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã –µ—Å–ª–∏ –æ—Ç–∫—Ä—ã—Ç–∞ –º–æ–¥–∞–ª–∫–∞
events.on('modal:open', () => {
    page.locked = true;
});
// ... –∏ —Ä–∞–∑–±–ª–æ–∫–∏—Ä—É–µ–º
events.on('modal:close', () => {
    page.locked = false;
});
// –û—Ç–∫—Ä—ã—Ç—å –∫–æ—Ä–∑–∏–Ω—É
events.on('bids:open', () => {
    modal.render({
        content: createElement('div', {}, basket.render())
    });
});
// –£–¥–∞–ª–∏—Ç—å —Ç–æ–≤–∞—Ä –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
events.on('basket:delete', (item) => {
    appData.deleteFromBasket(item.id);
    basket.total = appData.getTotalBasketPrice();
    page.counter = appData.getBasketAmount();
    basket.selected = appData.getBasketAmount();
    basket.renumerateItems();
    if (!appData.basket.length) {
        basket.items = [];
    }
});
// –û—Ç–∫—Ä—ã—Ç—å –æ–∫–Ω–æ –∑–∞–∫–∞–∑–∞ 
events.on('order:open', () => {
    modal.render({
        content: order.render({
            address: '',
            valid: false,
            errors: [],
        })
    });
});
// –û—Ç–∫—Ä—ã—Ç—å –æ–∫–Ω–æ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤ 
events.on('contacts:open', () => {
    modal.render({
        content: contacts.render({
            phone: '',
            email: '',
            valid: false,
            errors: [],
        })
    });
});
// –ò–∑–º–µ–Ω–∏–ª–æ—Å—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
events.on('orderFormErrors:change', (errors) => {
    const { address, payment } = errors;
    order.valid = (!address && !payment);
    order.errors = Object.values({ address, payment }).filter(i => !!i).join('; ');
});
// –ò–∑–º–µ–Ω–∏–ª–æ—Å—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
events.on('contactsFormErrors:change', (errors) => {
    const { email, phone } = errors;
    contacts.valid = (!email && !phone);
    contacts.errors = Object.values({ email, phone }).filter(i => !!i).join('; ');
});
// –ò–∑–º–µ–Ω–∏–ª–æ—Å—å –æ–¥–Ω–æ –∏–∑ –ø–æ–ª–µ–π
events.on('orderInput:change', (data) => {
    appData.setOrderField(data.field, data.value);
});
events.on('order:send', () => {
    appData.order.total = +appData.getTotalBasketPrice().split(' ')[0];
    appData.order.items = appData.basket.map(item => item.id);
    api
        .post('/order', appData.order)
        .then((res) => {
        appData.basket = [];
        basket.items = [];
        basket.selected = 0;
        basket.total = '0 —Å–∏–Ω–∞–ø—Å–æ–≤';
        appData.order = {
            items: [],
            total: null,
            address: '',
            email: '',
            phone: '',
            payment: ''
        };
        page.counter = 0;
        events.emit('order:finish', res);
    })
        .catch((err) => {
        console.error(err);
    });
});
// –û–∫–Ω–æ —É—Å–ø–µ—à–Ω–æ–π –ø–æ–∫—É–ø–∫–∏
events.on('order:finish', (res) => {
    modal.render({
        content: success.render({
            description: res.total
        })
    });
});
