import { CardModel } from "./CardModel";
import { Model } from "./base/Model";
export class AppModelData extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            address: '',
            payment: '',
            email: '',
            phone: '',
            total: 0,
            items: []
        };
        this.formErrors = {};
    }
    setCatalog(items) {
        this.catalog = items.map(item => new CardModel(item, this.events));
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    addToBasket(value) {
        this.basket.push(value);
    }
    getBasketAmount() {
        return this.basket.length;
    }
    getTotalBasketPrice() {
        const sum = this.basket.reduce((sum, next) => sum + next.price, 0);
        return sum + ' —Å–∏–Ω–∞–ø—Å–æ–≤';
    }
    deleteFromBasket(id) {
        let isDelited = false;
        this.basket = this.basket.filter(item => {
            if (item.id === id && !isDelited) {
                item.id !== id;
                isDelited = true;
                return false;
            }
            else {
                return true;
            }
        });
    }
    validateContacts() {
        const errors = {};
        if (!this.order.email) {
            errors.email = '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —É–∫–∞–∑–∞—Ç—å email';
        }
        if (!this.order.phone) {
            errors.phone = '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —É–∫–∞–∑–∞—Ç—å —Ç–µ–ª–µ—Ñ–æ–Ω';
        }
        this.formErrors = errors;
        this.events.emit('contactsFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateOrder() {
        const errors = {};
        if (!this.order.address) {
            errors.address = '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —É–∫–∞–∑–∞—Ç—å –∞–¥—Ä–µ—Å';
        }
        if (!this.order.payment) {
            errors.payment = '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —É–∫–∞–∑–∞—Ç—å —Å–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã';
        }
        this.formErrors = errors;
        this.events.emit('orderFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
        if (this.validateContacts()) {
            this.events.emit('contacts:ready', this.order);
        }
    }
}
