import { CDN_URL } from "../../utils/constants";
/**
 * –ë–∞–∑–æ–≤—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
 */
export class Component {
    constructor(container) {
        this.container = container;
        // –£—á–∏—Ç—ã–≤–∞–π—Ç–µ —á—Ç–æ –∫–æ–¥ –≤ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–µ –∏—Å–ø–æ–ª–Ω—è–µ—Ç—Å—è –î–û –≤—Å–µ—Ö –æ–±—ä—è–≤–ª–µ–Ω–∏–π –≤ –¥–æ—á–µ—Ä–Ω–µ–º –∫–ª–∞—Å—Å–µ
    }
    // –ò–Ω—Å—Ç—Ä—É–º–µ–Ω—Ç–∞—Ä–∏–π –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å DOM –≤ –¥–æ—á–µ—Ä–Ω–∏—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞—Ö
    // –ü–µ—Ä–µ–∫–ª—é—á–∏—Ç—å –∫–ª–∞—Å—Å
    toggleClass(element, className, force) {
        element.classList.toggle(className, force);
    }
    // –£—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —Ç–µ–∫—Å—Ç–æ–≤–æ–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ
    setText(element, value) {
        if (element) {
            element.textContent = String(value);
        }
    }
    // –°–º–µ–Ω–∏—Ç—å —Å—Ç–∞—Ç—É—Å –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏
    setDisabled(element, state) {
        if (element) {
            if (state)
                element.setAttribute('disabled', 'disabled');
            else
                element.removeAttribute('disabled');
        }
    }
    // –°–∫—Ä—ã—Ç—å
    setHidden(element) {
        element.style.display = 'none';
    }
    // –ü–æ–∫–∞–∑–∞—Ç—å
    setVisible(element) {
        element.style.removeProperty('display');
    }
    // –£—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å –∞–ª—Ç–µ—Ä–Ω–∞—Ç–∏–≤–Ω—ã–º —Ç–µ–∫—Å—Ç–æ–º
    setImage(element, src, alt) {
        if (element) {
            element.src = CDN_URL + src;
            if (alt) {
                element.alt = alt;
            }
        }
    }
    // –í–µ—Ä–Ω—É—Ç—å –∫–æ—Ä–Ω–µ–≤–æ–π DOM-—ç–ª–µ–º–µ–Ω—Ç
    render(data) {
        Object.assign(this, data !== null && data !== void 0 ? data : {});
        return this.container;
    }
}
