export function pascalToKebab(value) {
    return value.replace(/([a-z0‚Äì9])([A-Z])/g, "$1-$2").toLowerCase();
}
export function isSelector(x) {
    return (typeof x === "string") && x.length > 1;
}
export function isEmpty(value) {
    return value === null || value === undefined;
}
export function ensureAllElements(selectorElement, context = document) {
    if (isSelector(selectorElement)) {
        return Array.from(context.querySelectorAll(selectorElement));
    }
    if (selectorElement instanceof NodeList) {
        return Array.from(selectorElement);
    }
    if (Array.isArray(selectorElement)) {
        return selectorElement;
    }
    throw new Error(`Unknown selector element`);
}
export function ensureElement(selectorElement, context) {
    if (isSelector(selectorElement)) {
        const elements = ensureAllElements(selectorElement, context);
        if (elements.length > 1) {
            console.warn(`selector ${selectorElement} return more then one element`);
        }
        if (elements.length === 0) {
            throw new Error(`selector ${selectorElement} return nothing`);
        }
        return elements.pop();
    }
    if (selectorElement instanceof HTMLElement) {
        return selectorElement;
    }
    throw new Error('Unknown selector element');
}
export function cloneTemplate(query) {
    const template = ensureElement(query);
    return template.content.firstElementChild.cloneNode(true);
}
export function bem(block, element, modifier) {
    let name = block;
    if (element)
        name += `__${element}`;
    if (modifier)
        name += `_${modifier}`;
    return {
        name,
        class: `.${name}`
    };
}
export function getObjectProperties(obj, filter) {
    return Object.entries(Object.getOwnPropertyDescriptors(Object.getPrototypeOf(obj)))
        .filter(([name, prop]) => filter ? filter(name, prop) : (name !== 'constructor'))
        .map(([name, prop]) => name);
}
/**
 * –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç dataset –∞—Ç—Ä–∏–±—É—Ç—ã —ç–ª–µ–º–µ–Ω—Ç–∞
 */
export function setElementData(el, data) {
    for (const key in data) {
        el.dataset[key] = String(data[key]);
    }
}
/**
 * –ü–æ–ª—É—á–∞–µ—Ç —Ç–∏–ø–∏–∑–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∏–∑ dataset –∞—Ç—Ä–∏–±—É—Ç–æ–≤ —ç–ª–µ–º–µ–Ω—Ç–∞
 */
export function getElementData(el, scheme) {
    const data = {};
    for (const key in el.dataset) {
        data[key] = scheme[key](el.dataset[key]);
    }
    return data;
}
/**
 * –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—Ä–æ—Å—Ç–æ–π –æ–±—ä–µ–∫—Ç
 */
export function isPlainObject(obj) {
    const prototype = Object.getPrototypeOf(obj);
    return prototype === Object.getPrototypeOf({}) ||
        prototype === null;
}
export function isBoolean(v) {
    return typeof v === 'boolean';
}
/**
 * –§–∞–±—Ä–∏–∫–∞ DOM-—ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤ –ø—Ä–æ—Å—Ç–µ–π—à–µ–π —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏
 * –∑–¥–µ—Å—å –Ω–µ —É—á—Ç–µ–Ω–æ –º–Ω–æ–≥–æ —Ñ–∞–∫—Ç–æ—Ä–æ–≤
 * –≤ –∏–Ω—Ç–µ—Ä–Ω–µ—Ç –º–æ–∂–Ω–æ –Ω–∞–π—Ç–∏ –±–æ–ª–µ–µ –ø–æ–ª–Ω—ã–µ —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏
 */
export function createElement(tagName, props, children) {
    const element = document.createElement(tagName);
    if (props) {
        for (const key in props) {
            const value = props[key];
            if (isPlainObject(value) && key === 'dataset') {
                setElementData(element, value);
            }
            else {
                // @ts-expect-error fix indexing later
                element[key] = isBoolean(value) ? value : String(value);
            }
        }
    }
    if (children) {
        for (const child of Array.isArray(children) ? children : [children]) {
            element.append(child);
        }
    }
    return element;
}
// –§–æ—Ä–º–∏—Ä—É–µ—Ç –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–ª–∞—Å—Å–∞ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ 
export function createClassCategory(value) {
    let res;
    switch (value) {
        case '–¥—Ä—É–≥–æ–µ':
            res = 'other';
            break;
        case '—Å–æ—Ñ—Ç-—Å–∫–∏–ª':
            res = 'soft';
            break;
        case '–¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ–µ':
            res = 'additional';
            break;
        case '–∫–Ω–æ–ø–∫–∞':
            res = 'button';
            break;
        case '—Ö–∞—Ä–¥-—Å–∫–∏–ª':
            res = 'hard';
            break;
    }
    return 'card__category_' + res;
}
